/* src/styles/AuthCard.css */
.wrapper {
  --input-focus: #2d8cf0;
  --font-color: #323232;
  --font-color-sub: #666;
  --bg-color: #fff;
  --main-color: #323232;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding-top: 5vh;
}
/* switch card */
.switch {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50px;
  height: 20px;
}
.card-side::before {
  position: absolute;
  content: 'Log in';
  left: -70px;
  top: 0;
  width: 100px;
  text-decoration: underline;
  color: #f6f9fc;
  font-weight: 600;
}
.card-side::after {
  position: absolute;
  content: 'Sign up';
  left: 70px;
  top: 0;
  width: 100px;
  text-decoration: none;
  color: #f6f9fc;
  font-weight: 600;
}
.toggle {
  opacity: 0;
}
.slider {
  box-sizing: border-box;
  border-radius: 5px;
  border: 2px solid var(--main-color);
  box-shadow: 4px 4px var(--main-color);
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--bg-color);
  transition: 0.3s;
}
.slider:before {
  box-sizing: border-box;
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  border: 2px solid var(--main-color);
  border-radius: 5px;
  left: -2px;
  bottom: 2px;
  background-color: var(--bg-color);
  box-shadow: 0 3px 0 var(--main-color);
  transition: 0.3s;
}
.toggle:checked + .slider {
  background-color: var(--input-focus);
}
.toggle:checked + .slider:before {
  transform: translateX(30px);
}
.toggle:checked ~ .card-side:before {
  text-decoration: none;
}
.toggle:checked ~ .card-side:after {
  text-decoration: underline;
}
/* card */
.flip-card__inner {
  width: 300px;
  height: 400px;
  position: relative;
  perspective: 1000px;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}
.toggle:checked ~ .flip-card__inner {
  transform: rotateY(180deg);
}
.flip-card__front,
.flip-card__back {
  padding: 20px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  backface-visibility: hidden;
  background: lightgrey;
  gap: 20px;
  border-radius: 5px;
  border: 2px solid var(--main-color);
  box-shadow: 4px 4px var(--main-color);
}
.flip-card__back {
  transform: rotateY(180deg);
}
.flip-card__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.title {
  font-size: 25px;
  font-weight: 900;
  text-align: center;
  color: var(--main-color);
}
.flip-card__input {
  width: 250px;
  height: 40px;
  border-radius: 5px;
  border: 2px solid var(--main-color);
  background-color: var(--bg-color);
  box-shadow: 4px 4px var(--main-color);
  font-size: 15px;
  font-weight: 600;
  color: var(--font-color);
  padding: 5px 10px;
  outline: none;
}
.flip-card__input::placeholder {
  color: var(--font-color-sub);
  opacity: 0.8;
}
.flip-card__input:focus {
  border: 2px solid var(--input-focus);
}
.password-field {
  position: relative;
  width: 250px;
}
.show-password-btn {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  font-size: 0.875rem;
  color: var(--font-color-sub);
  cursor: pointer;
}
.flip-card__btn {
  width: 120px;
  height: 40px;
  border-radius: 5px;
  border: 2px solid var(--main-color);
  background-color: var(--bg-color);
  box-shadow: 4px 4px var(--main-color);
  font-size: 17px;
  font-weight: 600;
  color: var(--font-color);
  cursor: pointer;
}
.error {
  color: red;
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}
